<script>
	export default{
		data(){
			return{
				shoplist:''
			}
		},
		mounted(){
			this.$axios({
				method:'get',
				url:'src/data/find.json',
			})
			.then((response)=>{
				
				this.shoplist=response.data.shoplist;
				this.giftlist=response.data.giftlist;

				
			})
			.catch((response)=>{
				console.log(response)
			})
		}
