	    top: 0;
	    background: rgba(0,0,0,0.8);
	    color: #fff;
	    height: 20px;
	    width: 67%;
	    text-align: center;
	}
	.gift-info>p{
		font-size:14px;

	}
	.gift-info>strong{
		font-size:12px;
		color:red;

