<template>
	<main class="whole">
		<div class="first">
			<div class="head">
				<router-link to="/test" class="item">
					<span class="iconfont icon-shouye"></span>
				</router-link>
				<h1>跑跑飞送</h1>
				<div class="icon-size">
					<span class="iconfont icon-gouwuche"></span>
					<span class="iconfont icon-sousuo"></span>
				</div>
			</div>
		</div>
		<div class="content">
			<div class="search">
				<span class="iconfont icon-sousuo"></span>
				<input type="text" placeholder="请输入商家或商品">
			</div>
			
			<ul class="con-list">
				<li><router-link to="/recommend" class="item">推荐</router-link></li>
				<li><router-link to="/sup" class="item">超市便利</router-link></li>
				<li><router-link to="/fresh" class="item">生鲜果蔬</router-link></li>
				<li><router-link to="/healty" class="item">医药健康</router-link></li>
			</ul>
		</div>
		<div class="slide">
			<mt-swipe :auto="4000">
			  <mt-swipe-item><img src="../../assets/images/banner01.jpg" alt=""></mt-swipe-item>
			  <mt-swipe-item><img src="../../assets/images/banner02.jpg" alt=""></mt-swipe-item>
			  <mt-swipe-item><img src="../../assets/images/banner03.jpg" alt=""></mt-swipe-item>
			</mt-swipe>
		</div>

		<div class="cheaper">
			<h1>超值热卖</h1>
			<ul class="cheaper-list">
				<li v-for="(v,i) in cheaplist" >
					<router-link to="#" class="item">
						<div class="cheap-pic">
							<img :src="v.img" alt="">
						<span>{{v.hot}}</span>
						</div>
						<div class="cheap-info">
							<span><small>￥</small>{{v.price}}</span>
							<p>{{v.des}}</p>
							
							<span>{{v.shop}}|{{v.time}}分钟</span>
						</div>
					</router-link>
				</li>
			</ul>
		</div>

		<div class="around">
			<h1>附近商家</h1>
			<ul>
				<li v-for="v in storeName">
					<div class="storeName">
						<p>{{v.title}}</p>
						<div>
							<span>{{v.appraise}}</span>
							<span>{{v.numappr}}</span>
							<span>{{v.address}}</span>
						</div>	
					</div>
					<ol class="prolist">
						<li v-for="v in disprolist">
							<img :src="v.img" alt="" />
							<div>
								<p class="title">{{v.proname}}</p>
								<p class="des">{{v.des}}</p>
								<p class="price">
									<span>{{v.price}}</span>
									<del>{{v.delprice}}</del>
								</p>
								<p class="sale">{{v.sale}}</p>
							</div>
						</li>
					</ol>
				</li>
			</ul>
		<div class="loading"><mt-spinner type="fading-circle"></mt-spinner><span>加载中</span></div>

			
		</div>
		
			
	</main>
</template>

<script>
	export default{

		data(){
			return{
				cheaplist:'',
				
				storeName:'',
				disprolist:''
			}
		},
		mounted(){
			this.$axios({
				method:'get',
				url:'src/data/whole.json'
				})
					.then((response)=>{
						this.cheaplist=response.data.cheaplist;
						this.storeName=response.data.storeName;
						this.disprolist=response.data.disprolist;

			})
				
		}
	}
</script>

<style scoped>
	
	.first{
		height: 54px;
	}
	.head{
		display: flex;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		box-sizing: border-box;
		height: 55px;
		background: #fbc43c;
		line-height: 55px;
		padding: 0 15px;
		z-index: 2;
	}
	.head h1{
		flex: 1;
		font-size: 24px;
		font-weight: 400;
		text-align: center;
	}
	.icon-shouye,.icon-gouwuche,.icon-sousuo{
		display: flex;
		width: 50px;
		justify-content: center;
		font-size: 28px;
	}
	.icon-size{
		display: flex;
		width: 65px;
		justify-content: center;
		font-size: 28px;
	}
	.content{
		background-color:  #fbc43c;
		padding: 0 10px;
	}
	.search{
		position: relative;
		width: 100%;
		height: 44px;
	}
	.search input{
		margin: 7px 10px;
		width: 86%;
		height: 30px;
		border-radius: 3px;
		border: 1px;
		padding-left: 30px;
		color:#ccc;
		background-color: rgb(0, 0, 0.5);
	}
	.search .icon-sousuo{
		position: absolute;
		font-size: 1.5rem;
		color: #ccc;
		top: 9px;
	}
	.con-list{
		padding: 10px 0;
		width: 100%;
		display: flex;
	}
	.con-list li{
		flex: 1;
		justify-content: center;
		text-align: center;
		font-size: 14px;
		font-weight: 600;
	}
	.item{
		text-decoration: none;
		color: #000;
	}
	.slide{
		width:100%;
		height:15vh;
		margin-bottom: 40px;
	}
	.slide img{
		height: 100px;

	}
	img{
		width:100%;

	}
	.cheaper{
		padding: 0 15px;
	}
	.cheaper h1{
		font-size:24px;
		font-weight:bold;
		margin: 10px 0;
	}
	.cheaper-list{
		width: 100%;
		height: 64vh;
		display:flex;
		flex-wrap:wrap;
		justify-content:space-between;
	}
	.cheaper-list li{
		flex:1;
		flex-grow:0;
		margin: 10px 11px 10px 0;
	}
	.cheap-info span:nth-child(1){
		display:block;
		font-size:18px;
		color:red;
		font-weight:bold;
		margin: 5px 0;
	}
	.cheap-info span{
		display:block;
		font-size:12px;
		color:#c3c3c3;
		margin: 5px 0;
	}
	.cheap-info p{
		font-size:14px;
	}
	.cheap-pic{
		position:relative;
	}
	.cheap-pic img{
		width: 103px;
	}
	.cheap-pic span{
		position: absolute;
	    width: 45px;
	    height: 18px;
	    background-color: red;
	    color: #fff;
	    text-align: center;
	    bottom: 0;
	    font-size:12px;
	}
	.around{
		width: 100%;
	}
	.around h1{
		font-size:24px;
		font-weight:bold;
		margin: 10px 0;
		padding:0 15px;
	}
	.around ul{
		background:#f5f5f5;
	}
	.around ul li{
		margin-bottom: 20px;
		background: #fff;
	}
	
	.storeName{
		padding:10px  15px;
		box-sizing: border-box;
		border-bottom: 1px solid #ccc;
	}
	.storeName p{
		font-size: 17px;
		font-weight: bold;
	}
	.storeName div{
		margin-top: 10px;
		font-size: 12px;
		color: #666;
	}
	.storeName div span:nth-of-type(1){
		color: orange;
		margin-right: 30px;
	}
	.storeName div span:nth-of-type(3){
		margin-left: 85px;
	}
			
	.prolist li {
		position: relative;
		padding: 10px 20px;
		width: 100%;
		height: 110px;
		border-bottom: 1px solid #ccc;
		box-sizing: border-box;
		background: #fff;
		overflow: hidden;
		z-index: 0;
	}				
	.prolist li img {
		float: left;
		margin-right: 10px;
		width: 30%;
		height: 92%;
	}
	.prolist li .title {
		margin-right: 20px;
		font-size: 15px;
	}
	.prolist li .des {
		margin-top: 5px;
		font-size: 12px;
		color: #666;
	}
	
	.prolist li .price {
		margin-top: 10px;
		font-size: 13px;
		color: red;
	}
	
	.prolist li .price del {
		margin: 0 10px;
		color: #666;
		font-size: 12px;
	}
	
	.prolist li .dis {
		position: absolute;
		top: 10px;
		right: 20px;
		font-size:12px ;
		color: #aaa;
	}
	
	.prolist li .sale{
		position: absolute;
		bottom: 10px;
		right:20px;
		font-size: 12px;
		color: #ccc;
	}
	
	.prolist li .appraise {
		position: absolute;
		bottom: 40px;
		right: 20px;
		font-size: 12px;
		color: #e1b84b;
	}
	.loading{
		position: relative;
		margin: 10px auto;
		width: 36%;
	}
	.loading span:nth-of-type(2){
		position: absolute;
		top:5px ;left: 46px;
		color: #aaa;
	}
	

</style>


