// 引入相应的路由
import VueRouter from 'vue-router';
import index from '../component/content/index.vue'
import whole from '../component/content/whole.vue'
import my from '../component/content/my.vue'
import find from '../component/content/find.vue'
import test from '../component/content/test.vue'

export default new VueRouter({
	routes:[
		{path:'/',component:index},
		{path:'/index',component:index},
		{path:'/whole',component:whole},
		{path:'/find',component:find},
		{path:'/my',component:my},
		{path:'/test',component:test}


		// {path:'whole',component:whole}
		// {path:'/',redirect:'/index'}
	]
})



