<template id="index">

	<main class="index">
		<v-header></v-header>
		<div class="slide">
			<mt-swipe :auto="4000">
			  <mt-swipe-item><img src="../../assets/images/banner01.jpg" alt=""></mt-swipe-item>
			  <mt-swipe-item><img src="../../assets/images/banner02.jpg" alt=""></mt-swipe-item>
			  <mt-swipe-item><img src="../../assets/images/banner03.jpg" alt=""></mt-swipe-item>
			</mt-swipe>
		</div>
		<!-- 图标列表 -->
		<ul class="shop-list">
			<li>
				<span class="iconfont icon-zaocan"></span>
			</li>
			<li>
				<span class="iconfont icon-yiwuyaodian"></span>
			</li>
			<li>
				<span class="iconfont icon-jianzhi"></span>
			</li>
			<li>
				<span class="iconfont icon-mairu"></span>
			</li>
			<li>
				<span class="iconfont icon--shuiguolei"></span>
			</li>
		</ul>
		

		<div class="hot-sale">
			<div class="hot-sale-top">
				<h4>热门抢购</h4>
				<p>距开始
					<span>02</span>:<span>11</span>:<span>28</span><i class="iconfont icon-jiantou"></i>
				</p>
			</div>
			<ul class="hot-sale-list">
				<li v-for="v in hotlist">
					<img :src="v.img">
					<p>
						<span>{{v.nprice}}</span>
						<del>{{v.delprice}}</del>
					</p>
				</li>
			</ul>
		</div>
		<div class="need">
			<div class="need-top">
				<h4>猜你需要</h4>
				<p><span>更多选择</span><i class="iconfont icon-jiantou"></i></p>
			</div>
			<ul class="need-list">
				<li v-for="(v,i) in needlist" @click="enter(i)">
					<router-link to="/detail">
						<img :src="v.img">
						<div>
							<p class="title">{{v.title}}</p>
							<p class="des">{{v.des}}</p>
							<p class="price">
								<span>{{v.price}}</span>
								<span>{{v.delprice}}</span>
								<span>{{v.discount}}</span>
							</p>
							<p class="dis">{{v.dis}}</p>
							<p class="appraise">{{v.appraise}}</p>
						</div>
					</router-link>
				</li>
			</ul>
			<div class="loading">
				
			</div>
			
		</div>


	</main>
</template>

<script>
	import header from '../nav/header.vue'
	import footer from '../nav/footer.vue'
	export default {

      components:{
      	'v-header':header,
      	'v-footer':footer
      },
      data(){
      	return {
      		hotlist:'',
	      	needlist:'',
	      	addrs:'',
	      	
      	}
      },
      mounted(){
      	console.log('进来了')
      	this.$axios({
      		method:'get',
      		url:'src/data/index.json'
      	})
      		.then((response)=>{
      			this.hotlist=response.data.hotlist;
      			// console.log(response.data.hotlist)
      			this.needlist=response.data.needlist
      	})
      		.catch((response)=>{
      			console.log(response)
      		})
      },
      methods:{
      	enter(i){

      	}
  }
}
</script>

<style scoped>
	body{
		padding:0;
	}
	.slide{
		height:23vh;
	}
	img{
		width:100%;
	}
	.shop-list{
		display: flex;
		padding: 0;
		margin: 0;
		list-style:none;
		height: 71px;
		border-bottom:1px solid #ccc;

	}
	.shop-list>li{
		flex:1;
		float: left;
		margin:10px;
		text-align: center;
		border-radius:50%; 
		padding: 3px 0 0;
	}
	.shop-list>li:nth-child(1){
		background-color: #ed7549;
	}
	.shop-list>li:nth-child(2){
		background-color: #707785;
	}
	.shop-list>li:nth-child(3){
		background-color: #eb6767;
	}
	.shop-list>li:nth-child(4){
		background-color: #ea5e5c;
	}
	.shop-list>li:nth-child(5){
		background-color: #68bfbc;
	}
	span{
		width: 100px;
		height: 100px;
		
	}
	.shop-list>li .iconfont{
		width: 80px;
		height: 80px;
		font-size:2rem;
		font-weight:400;
		color: #fff;
		position:relative;
		top:5px;
	}
	.hot-sale{
		width: 100%;
		padding:10px 20px;
		box-sizing: border-box;
		border-bottom: 1px solid #ccc;
	}
	.hot-sale-top{
		width: 100%;
		height: 50px;
	}
	.hot-sale h4{
		float: left;
		margin: 10px 0;
	}
	.hot-sale p{
		float: right;
		margin-top: 13px;
		width: 138px;
		font-size: 12px;
	}
	.hot-sale-top p>span{
		display: inline-block;
		margin: 0 5px;
		width: 16px;
		height: 16px;
		background: #505050;
		border-radius: 5px;
		color: #fff;
		font-size: 12px;
		text-align: center;
		line-height: 16px;
	}
	.icon-jiantou{
		font-size: 14px;
		font-weight: bold;
	}
	.hot-sale-list{
		height: 135px;
		padding: 0;
		margin: 0;
	}
	.hot-sale-list li{
		list-style: none;
		float: left;
		width: 33.33%;
		height:92px;
		text-align: center;
	}
	.hot-sale-list li img{
		width: 54%;
		height: 100%;
	}
	.hot-sale-list li span{
		margin-right: 10px;
		font-size: 14px;
		color: red;
		margin-left: 25px;
	}
	.hot-sale-list li del{
		font-size: 12px;
		color: red;
	}
	
	.need{
		width: 100%;
		padding: 10px 20px;
		box-sizing: border-box;
		border-bottom: 1px solid #ccc;
	}
	.need-top{
		width: 100%;
		overflow: hidden;
	}
	.need-top>h4{
		float: left;
	}
	.need-top>p{
		display: inline-block;
		float: right;
		margin-top: 5px;
		width: 73px;
		font-size: 12px;
	}
	.need-top p span{
		margin-right: 6px;
	}
	
	.need-list{
		margin-top: 20px;
	}
	.need-list li{
		position: relative;
		margin-top: 20px;
		width: 100%;
		height: 90px;
	}
	.need-list li img{
		float: left;
		margin-right: 20px;
		width: 25%;
		height: 80%;
	}
	.need-list li a{
		text-decoration:none;
	}
	.need-list li a .title{
		margin-right: 20px;
		font-size: 15px;
		color: #222;
	}
	.need-list li .des{
		margin-top: 5px;
		font-size: 12px;
		color: #666;
	}
	.need-list li .price{
		margin-top: 5px;
		font-size: 12px;
		color: red;
	}
	.need-list li .price del{
		margin: 0 10px;
		color:#666;
		font-size: 12px;
	}
	.need-list li .price span:nth-of-type(2){
		width: 50px;
		height: 30px;
		padding: 1px;
		border: 1px solid red;
		border-radius:5px ;
		color: red;
		font-size: 12px;
	}
	.need-list li .dis{
		position: absolute;
		top:2px;right: 0;
		font-size:12px ;
		color:#aaa;
	}
	.need-list li .appraise{
		position: absolute;
		bottom:10px;right: 0;
		font-size:12px ;
		color:#e1b84b;
	}
			
</style>


